import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/aboutself/home'
import Person from './views/aboutself/person'
import Information from './views/aboutself/Information'
import Job from './views/aboutself/job'
import Design from './views/aboutself/design'
import Experience from './views/aboutself/experience'
import DesiginContent from './components/desigincontent'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/person',
      name: 'person',
      component: Person
    },
    {
      path: '/information',
      name: 'information',
      component: Information
    },
    {
      path: '/design',
      name: 'design',
      component: Design,
      children:[
        {
          path: '/desigincontent',
          name: 'desigincontent',
          component: DesiginContent,
        }
      ]
    },
    {
      path: '/experience',
      name: 'experience',
      component: Experience
    },
    {
      path: '/job',
      name: 'job',
      component: Job
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
