import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './registerServiceWorker'
import axios from 'axios'

Vue.use(ElementUI)
Vue.prototype.$axios=axios
axios.defaults.baseURL="https://person-76570.firebaseio.com/api/"
Vue.config.productionTip = false
Vue.filter(
  'fltime',function(el){
       let time=new Date(el)
       var n=time.getFullYear()
       var y=(time.getMonth()+1).toString().padStart(2,'0')
       var r=time.getDate().toString().padStart(2,'0')
       var s=time.getHours().toString().padStart(2,'0')
       var f=time.getMinutes().toString().padStart(2,'0')
       var m=time.getSeconds().toString().padStart(2,'0')
       return `${n}-${y}-${r} ${s}:${f}:${m}`
  }
)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
