<template>
    <div class="person"
            v-loading="loading2"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
        <el-row>
            <div class="img"></div>
        </el-row>
        <el-row class="center">
             <el-col :span="8"><strong><i class="el-icon-menu"></i>基本信息</strong></el-col>
        </el-row>
        <el-row class="center" v-for="(item,index) in base" :key="index"> 
             <el-col :span="8" v-if="item.key"><i class="el-icon-view"></i>{{item.key}}：{{item.name}}</el-col>
             <el-col :span="8" v-if="item.sex"><i class="el-icon-view"></i>{{item.sex}}：{{item.sexs}}</el-col>
             <el-col :span="8" v-if="item.per"><i class="el-icon-view"></i>{{item.per}}：{{item.pers}}</el-col>
        </el-row>
        <el-row class="center">
             <el-col :span="8"><strong><i class="el-icon-menu"></i>基本资料</strong></el-col>
        </el-row>
         <el-row
             class="center" v-if="updata.arr" v-for="(item,index) in updata.arr" :key="index+ '-label'"> 
             <el-col :span="8" v-if="item.keys"><i :class="[index>0?'el-icon-message':'el-icon-view']"></i>{{item.keys}}：{{item.name}}</el-col>
             <el-col :span="8" v-if="item.sex"><i :class="[index>0?'el-icon-phone':'el-icon-view']"></i>{{item.sex}}：{{item.sexs}}</el-col>
             <el-col :span="8" v-if="item.per"><i class="el-icon-view"></i>{{item.per}}：{{item.pers}}</el-col>
        </el-row>
        
    </div>
</template>
<script>
export default {
    data(){
      return{
           base:[
           {key:"姓名",name:"陈凯",sex:"性别",sexs:"男",per:"籍贯",pers:"四川、成都"},
           {key:"民族",name:"汉",sex:"出生年月",sexs:"1997、9、23",per:"专业",pers:"计算机应用技术"},
           {key:"学历",name:"大专",sex:"",sexs:"",per:"",pers:""}
                ],
       updata:[],
       loading2:true
      }
    },
    created(){
        this.getupdata()
    },
    methods:{
        getupdata(){
            this.$axios.get("information.json")
            .then(re=>{
                if(re.status==200){
                   for(let key in re.data){
                     this.updata=re.data[key]
                   }
                   this.loading2 = false
                }
            })
            .catch(err=>console.log(err))
        }
        }

}
</script>
<style lang="scss" scoped>
.person{

    .img{
        background: url("../../assets/com.png")no-repeat center center;
        width: 160px;
        height: 160px;
        background-size: cover;
        border-radius: 50%;
        margin-left: 60px;
        margin-top: 10px;
        box-shadow: 0px 4px 4px #000;
        position:relative;
        animation: move 10s infinite ease-in;
        -webkit-animation:  move 10s infinite ease-in;
        -moz-animation: move 10s infinite ease-in;
        -o-animation: move 10s infinite ease-in;
        -ms-zoom-animation: move 10s infinite ease-in;
    }
    .img:hover{
        animation: none;
        cursor: pointer;
    }
    @keyframes move {
        0%{
            transform: rotateX(1deg);
           -webkit-transform:rotateX(1deg);
           -moz-transform: rotateX(1deg);
           -o-transform: rotateX(1deg);
           -ms-transform: rotateX(1deg);
        }
         25%{
            transform:scale(1.05);
            -webkit-transform:scale(1.05);
           -moz-transform: scale(1.05);
           -o-transform: scale(1.05);
           -ms-transform: scale(1.05);
        }
         40%{
            transform: rotateX(180deg);
             -webkit-transform:rotateX(180deg);
           -moz-transform: rotateX(180deg);
           -o-transform: rotateX(180deg);
           -ms-transform: rotateX(180deg);
        }
        70%{
            transform: rotateX(360deg);
            -webkit-transform:rotateX(360deg);
            -moz-transform: rotateX(360deg);
            -o-transform: rotateX(360deg);
            -ms-transform: rotateX(360deg);
            transform: translateX(600px);
            -webkit-transform:translateX(600px);
            -moz-transform: translateX(600px);
            -o-transform: translateX(600px);
            -ms-transform: translateX(600px);
        }
         100%{
            transform: rotateX(360deg);
             -webkit-transform:rotateX(360deg);
            -moz-transform: rotateX(360deg);
            -o-transform: rotateX(360deg);
            -ms-transform: rotateX(360deg);
            transform: translateX(0px);
             -webkit-transform:translateX(0px);
            -moz-transform: translateX(0px);
            -o-transform: translateX(0px);
            -ms-transform: translateX(0px);
        }
    }
    .center{
        margin-top: 10px;
        margin-left: 60px;
        strong{
            color: rgb(161, 161, 161);
        }
    }

}
</style>
