<template>
    <div class="design"
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
    :data="tableData"
    style="width: 100%"
    >

  <!-- 删选 -->
  <div class="top">
      <el-row>
        <div class="top_searh">
        <el-form
        :model="searchtime"
        ref="add_data"
        :inline="true">
        <el-form-item label="按照时间筛选"
        class="itemspadd">
        <el-date-picker
         v-model="searchtime.value1"
         type="datetime"
         placeholder="选择开始日期时间">
        </el-date-picker>--
        <el-date-picker
           v-model="searchtime.value2"
           type="datetime"
          placeholder="选择结束日期时间">
        </el-date-picker>
         </el-form-item>
        <el-button type="primary" class="search" @click="searchfn()">删选</el-button>
        </el-form>
        </div>
          <desigin-content @getadd="adds($event)" :bjdata="bjdatas" v-show="true"></desigin-content>
      </el-row>
  </div>


    <!--表格数据 -->
  <el-table  
    v-if="tableData.length>0"
    :data="tableDatas"
    max-height="450"
    border
    style="width: 100%">
    <el-table-column
      type="index"
      label="序号"
      width="70"
      align="center"
     >
    </el-table-column>
    <el-table-column
      label="日期"
      prop="time"
      align="center"
      width="260">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.time | fltime }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="类型"
      align="center"
      prop="type"
      width="180">
    </el-table-column>
    <el-table-column
      label="描述"
      prop="desgreption"
      align="center"
      width="180">
    </el-table-column>
    <el-table-column
      label="收入"
      prop="push"
      align="center"
      width="130">
      <template slot-scope="scope">
        <span style="color:blue">{{ scope.row.push }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="支出"
      align="center"
      prop="out"
      width="130">
       <template slot-scope="scope">
        <span style="color:red">{{ scope.row.out }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="账户现金"
      prop="sumprice"
      align="center"
      width="130">
      <template slot-scope="scope">
        <span style="color:green">{{ scope.row.sumprice }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="备注"
      align="center"
      prop="remarks"
      width="180">
    </el-table-column>
     <el-table-column align="center" label="操作" width="180">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>



    <!-- 实现分页 -->
 <el-row>
     <el-col :span="24">
         <div class="delpage">
           <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="page.currentPage"
            :page-sizes="page.page_sizes"
            :page-size="page.page_size"
            :layout="page.layoutpage"
            :total="page.total">
          </el-pagination>
         </div>
     </el-col>
 </el-row>


 <!-- 其他dome -->
 <el-row class="center">
     <el-col :span="4">
      <div>
         <span><i class="el-icon-more"></i>其他小dome...等<i class="el-icon-more"></i></span>
      </div>    
    </el-col> 
    <el-col :span="4">
      <div>
         <a href="https://chenkai13145.github.io/dist" target="blank">dome1</a>
      </div>    
    </el-col> 
    <el-col :span="4">
       <div class="last">
         <a href="https://chenkai13145.github.io/bj" target="blank">dome2</a><span>等.....</span>
      </div>       
    </el-col> 
 </el-row>
 </div>
</template>
<script>
import DesiginContent from '../../components/desigincontent'
export default {
    data(){
        return{
            loading2: true,
            tableData:[],
            alltableData:[],
            filterdata:[],
            bjdata:{
                trun:"",
                title:"",
                form:"",
                three:""
            },
            page:{
                total:30,//总数
                page_size:5,//一页显示多少条
                currentPage:1,// 当前页
                page_sizes:[5,10,15,20],//每页显示多少条
                layoutpage:"total, sizes, prev, pager, next, jumper",//翻页属性
            },
            searchtime:{
                value1: '',
                value2: '',
            }
        }
    },
    created(){
       this.getmoney()
    },
    methods:{
        getmoney(){
            this.$axios.get("money.json")
            .then(res=>{
              let obj=res.data
              let arr=[]
              for(let key in obj){
                  obj[key].id=key
                  arr.push(obj[key])
              }
              if(arr.length>0){
                 this.tableData=arr
                 this.alltableData=arr
                 this.filterdata=arr
                 this.loading2=false
                 //设置分页数据
                 this.setPage()
              }
            })
            .catch(err=>console.log(err))
        },
        setPage(){
            //分页属性
                  this.page.total=this.alltableData.length
                  this.page.page_size=5
                  this.page.currentPage=1
            //设置默认的分页数据
                  this.tableData=this.alltableData.filter((item,index)=>{
                      return index<this.page.page_size
                  })
        },
        handleEdit(index,row){
            // 编辑
                this.bjdata.title="编辑资金信息"
                this.bjdata.form=row
                this.bjdata.trun=true
                this.bjdata.three='updt'
        },
        handleDelete(index,row){
            //删除
                this.bjdata.title="删除资金信息"
                this.bjdata.form=row
                this.bjdata.trun=true
                this.bjdata.three='del'
        },
        adds(add){
           if(add.types=="updt"){
               //修改成功后数组变化
               this.tableData.some((updtres,uindex)=>{
                     if(updtres.id==add.id){
                        // this.tableData.splice(uindex,1)
                        // this.tableData.push(add.updt)
                        this.getmoney()
                        this.$message({
                          message: '恭喜你，修改成功',
                          type: 'success'
                         });
                         return true
                     }
               })
           }else if(add.types=="del"){
               //删除成功后数组变化
               this.tableData.some((delres,indexs)=>{
                   if(delres.id==add.id){
                        // this.tableData.splice(indexs,1)
                        this.getmoney()
                        this.$message({
                            message: '删除成功',
                            });
                         return true
                   }
               })
           }else{
            // 添加成功后数组变化
                //    this.tableData.unshift(add)
                   this.getmoney()
                   this.$message({
                     message: '恭喜你，添加成功',
                      type: 'success'
                     });
           }
        
        },
        handleSizeChange(page_size){//一页条数
               this.page.page_size=page_size
               this.tableData=this.alltableData.filter((item,index)=>{
                   return index<page_size
               })
        },
        handleCurrentChange(pages){//当前每一页显示的条数方法
             this.page.currentPage=pages
             let index=this.page.page_size*(pages-1)
             let sum=this.page.page_size*pages
             let arrpage=[]
             for(var i=index;i<sum;i++){
                 if(this.alltableData[i]){
                    arrpage.push(this.alltableData[i])
                 }
             }
             if(arrpage.length>0){
                 this.tableData=arrpage
             }
        },
        searchfn(){//删选
           if(!this.searchtime.value1 || !this.searchtime.value2){
               this.$message({
                   type:'warning',
                   message:'请选择时间区域'
               })
               return;
           }
           const stime=this.searchtime.value1.getTime()
           const etime=this.searchtime.value2.getTime()
           this.alltableData=this.filterdata.filter((item,index)=>{
               let date=new Date(item.time)
               let time=date.getTime()
               return time >=stime && time<=etime
           })
           this.setPage()
        }

    },
    computed:{
        tableDatas(){
            return this.tableData
        },
        bjdatas(){
            return this.bjdata
        },
    },
    components:{
        "desigin-content":DesiginContent
    }
}
</script>
<style lang="scss" scoped>
.design{
    padding: 10px;
    .top{
        .top_searh{
            float:left;
            .search{
                margin-left: 20px;

            }
            .itemspadd{
                margin-bottom: 0;
                padding-bottom: 0;
            }
        }
        margin-bottom: 10px;
    }
    .delpage{
        text-align:right;
        margin-top: 10px;
    }
    .center{
        text-align: center;
        margin-top: 10px;
        .last{
            span{
                color: rgb(189, 187, 185);
                display: inline-block;
                padding-left: 10px;
            }
        }
    }
}
</style>
