<template>
  <div class="desigincontent">
<!-- Form -->
<el-button type="primary" class="btn" round  @click="addclear()">添加</el-button>
<el-dialog :title="bjdata.title"
   :visible.sync="bjdata.trun">
  <el-form :model="form" :mm="up" :rules="form_rules" ref="form">
    <el-form-item label="备注" prop="remarks" :label-width="formLabelWidth">
      <el-input v-model="form.remarks" placeholder="请输入备注信息" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="类型" prop="type" :label-width="formLabelWidth">
      <el-select v-model="form.type" placeholder="请选择类型">
        <el-option v-if="item" :label="item" v-for="(item,index) in typearr" :key="index" :value="item"></el-option>
      </el-select>
    </el-form-item>
     <el-form-item label="支出" prop="out" :label-width="formLabelWidth">
      <el-input type="number" v-model="form.out" placeholder="请输入支出信息" autocomplete="off"></el-input>
    </el-form-item>
     <el-form-item label="收入" prop="push" :label-width="formLabelWidth">
      <el-input type="number" v-model="form.push" placeholder="请输入收入信息" autocomplete="off"></el-input>
    </el-form-item>
     <el-form-item label="账户现金" prop="sumprice" :label-width="formLabelWidth">
      <el-input type="number" v-model="form.sumprice" placeholder="请输入账户现金信息" autocomplete="off"></el-input>
    </el-form-item>
     <el-form-item label="描述" :label-width="formLabelWidth">
      <el-input type="textarea" v-model="form.desgreption" placeholder="请输入描述信息" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item class="text_right">
        <el-button @click="bjdata.trun = false">取消</el-button>
        <el-button type="primary"  @click="postform('form')">确认</el-button>
    </el-form-item>
  </el-form>
</el-dialog>
  </div>
</template>

<script>
export default {
 data(){
     return{
        dialogFormVisible: false,
        form: {
          time: new Date(),
          type: '',
          desgreption: '',
          push: '',
          out: '',
          sumprice: '',
          remarks: '',
        },
        formLabelWidth: '120px',
        typearr:["优惠券","提现","刷卡","充值券","现金","支付宝"],
        form_rules:{
           remarks:[
               { required:true,message:"备注不能为空",trigger:'blur'}
           ],
           sumprice:[
               { required:true,message:"账户现金不能为空",trigger:'blur'}
           ],
           out:[
               { required:true,message:"支出不能为空",trigger:'blur'}
           ],
           push:[
               { required:true,message:"收入不能为空",trigger:'blur'}
           ]
        }
      }
    },
    methods:{
    postform(formName){
            this.$refs[formName].validate(valid=>{
                if(valid){
                    if(this.bjdata.three=="updt"){
                   this.$axios.put("money/"+this.bjdata.form.id+".json",this.form)
                   .then(res=>{
                       this.$emit('getadd',{id:this.bjdata.form.id,updt:this.form,types:"updt"})
                       this.bjdata.trun=false
                   })
                   .catch(err=>console.log(err))
                   }else if(this.bjdata.three=="del"){
                       this.$axios.delete("money/"+this.bjdata.form.id+".json")
                   .then(res=>{
                       this.$emit('getadd',{id:this.bjdata.form.id,types:"del"})
                       this.bjdata.trun=false
                   })
                   .catch(err=>console.log(err))
                   }else{
                        this.$axios.post("money.json",this.form)
                   .then(res=>{
                       this.$emit('getadd',this.form)
                       this.bjdata.trun=false
                   })
                   .catch(err=>console.log(err))
                   }
                }
            })
            
        },
    addclear(){//添加并清空
        this.bjdata.title="添加资金信息"
        this.bjdata.trun = true
        this.form.type=''
        this.form.desgreption=''
        this.form.push=''
        this.form.out=''
        this.form.sumprice=''
        this.form.remarks=''
        }
    },
    props:["bjdata"],
   created(){
       this.bjdata.trun=false
   },
   computed:{
     up(){
       this.form.type=this.bjdata.form.type
       this.form.desgreption=this.bjdata.form.desgreption
       this.form.push=this.bjdata.form.push
       this.form.out=this.bjdata.form.out
       this.form.sumprice=this.bjdata.form.sumprice
       this.form.remarks=this.bjdata.form.remarks
        }
   }

  }
</script>

<style scoped lang="scss">
.desigincontent{
    .btn{
            float: right;
        }
        .text_right{
           margin: 0 auto !important;
           text-align: center;
        }
        }
 
</style>
