<template>
<div class="information">
       
<el-row>
        <div class="book_name">
          <span 
           type="primary"
           @click="getbook('webWebVuexES6HTML5CSS3JSJQueryAjaxVuejsWebpackBootstrap')">前端:</span><strong v-if="websql" v-html="websql.web"></strong>  
        </div>
        <div class="book_name">
          <span 
          type="primary"
          @click="getbook('SQLServerMVCC#/NET')">后端：</span><strong v-if="websql" v-html="websql.sql"></strong>
        </div>
</el-row>



<el-row class="center">
    <el-col><i class="el-icon-share"></i><span  @click="getbook('')">(恢复)列如：</span></el-col>
</el-row>



<el-row class="bottom">
  <el-col>
    <el-card v-for="(item, index) in data" :key="index" :body-style="{ padding: '0px',width:'220px'}" class="outline" shadow="hover">
      <div v-if="item.src" :style="{ 'background':'url('+item.src+')no-repeat','background-size':'100% 124px !important'}"
       class="iamge" :title="item.sl"></div>
      <div style="padding: 14px;">
        <span>{{item.title}}</span>
        <div class="bottom clearfix">
          <time class="time">{{ currentDate | fltime }}</time>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
             currentDate: new Date(),
             data:[],
             websql:{}
        }
    },
    created(){
       this.getbook()
    },
    methods:{
      getbook(Strng){
         const loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
                  });
        this.$axios.get("bookshow.json")
        .then(res=>{
          if(res.status==200){
            let arr=[]
            let obj=res.data
            for(let key in obj){
              if(key!="web"){
              obj[key].id=key
              arr.push(obj[key])
              }else{
                let webs=obj[key]
                for(let keys in webs){
                  this.websql=webs[keys]
                }
              }
            }
            if(arr.length>0){
              if(!Strng){
                this.data=arr
              }else{
                this.data=arr.filter(fres=>{
                  let Strr=(fres.title).toString()
                  let Str=Strr.slice(0,3)
                   return Strng.indexOf(Str)!=-1
                })
              }
             if(this.data.length>0){
                  loading.close();
             }
            }
          }
        
        })
        .catch(err=>console.log(err))
      }
      
    },
    // filters:{
    //   fltime(el){
    //     let time=new Date(el)
    //     var n=time.getFullYear()
    //     var y=(time.getMonth()+1).toString().padStart(2,'0')
    //     var r=time.getDate().toString().padStart(2,'0')
    //     var s=time.getHours().toString().padStart(2,'0')
    //     var f=time.getMinutes().toString().padStart(2,'0')
    //     var m=time.getSeconds().toString().padStart(2,'0')
    //     return `${n}-${y}-${r} ${s}:${f}:${m}`
    //   }
    // },
  
}
</script>
<style lang="scss" scoped>
.information{
   
  
  

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
  .book_name{
    padding: 10px;
    span{
      cursor: pointer;
      display: inline-block;
      color: red;
      margin-right: 10px;
    }
    span:hover{
      color: black;
    }
  }
  .center{
    font-weight:bold;
    margin-left: 30px;
    i{
      color: #999;
      margin-right: 10px;
    }
    span{
      cursor: pointer;
    }
  }
  .bottom{
    margin: 10px;
    .outline{
      width: 220px;
      float: left;
      margin-left: 10px;
      margin-top: 10px;
      height: 242px;
      padding: 0 10px;
      background: white;
      position: relative;
    }
    .iamge{
      width: 100%;
      height: 124px;
     background-size:100% 124px !important;
    }
    .iamge:hover{
      cursor: pointer;
    }
     .time {
    font-size: 10px;
    color: #999;
    position: absolute;
    bottom: 3px;
  
  }
  }
}
</style>
