<template>
  <div id="app">
  <el-row>
     <app-header :indexnum="numindex" :navdata="navdata"></app-header>
  </el-row>
  <el-row>
  <el-col :span="3">
  <el-menu
  class="el-menu-demo text-center"
  mode="vertical"
  background-color="#545c64"
  text-color="#fff"
  active-text-color="#ffd04b">
  <router-link tag="span" :to="{name:routers[index]}" v-for="(item,index) in navdata" :key="index">
  <el-menu-item :index="(index+1).toString()"  @click="indexs(index)">
    {{item}}
  </el-menu-item>
  </router-link> 
  </el-menu>

  </el-col>
  <el-col :span="20">
   <router-view></router-view>
  </el-col>
</el-row>
  

   
  </div>
</template>

<script>
import Header from './views/header/header'
export default {
  data(){
    return{
     navdata:[],
     indexnum:0,
     routers:["home","person","information","design","experience","job"],
     loading2:true
    }
  },
  created(){
    this.getdata()
    if(this.indexnum==0){
      this.$router.push("/home")
    }
  },
  components:{
    "app-header":Header
  },
  methods:{
    getdata(){
        this.$axios.get("person.json")
        .then(res=>{
         let obj=res.data
         for(let key in obj){
           this.navdata=obj[key].arry
         }
         })
        .catch(err=>console.log(err))
    },
    indexs(index){
      this.indexnum=index
    }
  },
   computed:{
        numindex(){
          return this.indexnum
        }
  },
  watch:{
    $route(val){
      this.indexnum=this.routers.findIndex(item=>{
        return item==val.name
      })
    }
  }
  

}
</script>

<style lang="scss">
*{
  padding: 0;
  margin: 0;
}
#app{
   overflow: hidden;
  background:url("assets/bg.png") no-repeat;
  background-size: cover;
   .is-active{
    color: black;
  }
  .el-menu-demo{
    height:100vh !important;
    padding-bottom: 60px;
  }
}
</style>
